import React from "react";
import { graphql } from "gatsby";
import { ProfileSection } from "../components/intro-section/profile";
import { ArticleNavSection } from "../components/article-nav";
import Layout from "../components/layout";
import SeoHelmet from "../components/seo";

const Home = ({ data, pageContext }) => {
  const articlesInfoProps = data.allMdx;
  const { skip } = pageContext;

  const pathname = pageContext.skip > 0 ? `/page/${skip / 10 + 1}` : null;

  return (
    <>
      <SeoHelmet pathname={pathname} />
      <Layout>
        <ProfileSection />
        <ArticleNavSection articlesInfoProps={articlesInfoProps} />
      </Layout>
    </>
  );
};

export const query = graphql`
  query ($skip: Int!) {
    allMdx(
      sort: { fields: frontmatter___date, order: DESC }
      limit: 10
      skip: $skip
    ) {
      nodes {
        frontmatter {
          title
          description
          date(formatString: "MMMM D, YYYY")
          category
          tags
          hero_image {
            childImageSharp {
              gatsbyImageData(width: 900, aspectRatio: 1.4)
            }
          }
          hero_image_alt
        }
        id
        slug
      }
      pageInfo {
        currentPage
        hasNextPage
        hasPreviousPage
        pageCount
        totalCount
      }
    }
  }
`;

export default Home;
